footer {
    height: 50px;
    background-color: rgba(255, 255, 255, 0.8);
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.footer-button {
    border: 1px solid black;
    border-radius: 5px;
    padding: 0.5em;
}

.footer-button:hover {
    cursor: pointer;
}